import React from 'react';
import Home from "./Home"
import Story from "./Story"
import {Route} from "react-router-dom";
import './App.css';
import Work from "./Work";
import Ui_ux from "./components/work/Ui_ux";
import Identity from "./components/work/Identity";
import Web from "./components/work/Web";
import Topher from "./components/topher/Topher";
import Codekids from "./components/codekids/Codekids";
import Spex from "./components/spex/Spex";
import Kasone from "./components/kasone/Kasone";
